.newsCard__wrapper {
  width: 705px;
  height: 540px;

  border-radius: 4px;
  background: #fff;

  box-shadow: 0px 4px 4px -4px rgba(30, 33, 44, 0.03),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 80px 80px -20px rgba(154, 156, 165, 0.08);
}

.newsCard__wrapper.newsCard__wrapper--home {
  @media (max-width: 768px) {
    width: 100%;
    object-fit: contain;
  }
}

.newsCard__wrapperList-newsCategory {
  width: 600px;
  height: 515px;

  border-radius: 4px;
  background: #fff;

  box-shadow: 0px 4px 4px -4px rgba(30, 33, 44, 0.03),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 80px 80px -20px rgba(154, 156, 165, 0.08);

  @media (max-width: 768px) {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.newsCard__wrapper--small {
  width: 495px;
  min-height: 255px;

  border-radius: 4px;
  background: #fff;

  box-shadow: 0px 4px 4px -4px rgba(30, 33, 44, 0.03),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 80px 80px -20px rgba(154, 156, 165, 0.08);
}

.newsCard__img {
  width: 705px;
  height: 360px;
  object-fit: contain;
  border-radius: 4px 4px 0px 0px;

  @media (max-width: 768px) {
    width: 100%;
    height: 200px;
    object-fit: contain;
  }
}

.newsCard__img--newsCategory {
  width: 600px;
  height: 306px;

  border-radius: 4px 4px 0px 0px;

  @media (max-width: 768px) {
    width: 100%;
    object-fit: contain;
    padding: 20px 0;
  }

  @media (max-width: 500px) {
    margin-bottom: -30px;
  }

  @media (max-width: 400px) {
    margin-bottom: -60px;
  }
}

.newsCard__img--small {
  width: 495px;
  height: 156px;

  border-radius: 4px 4px 0px 0px;
}

.newsCard__conteiner {
  padding: 24px;
}

.newsCard__conteiner--small {
  padding: 16px 24px 24px 24px;
}

.newsCard__title {
  color: #1e212c;

  font-family: Ubuntu;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;

  transition: color 0.5s cubic-bezier(0.23, 1, 0.32, 1);

  @media (max-width: 450px) {
    text-align: center;
  }
}

.newsCard__title:hover,
.newsCard__title:focus {
  color: #ff5a30;
}

.newsCard__wrapper--infoBlock {
  margin-top: 12px;

  display: flex;
  align-items: center;

  color: #787a80;
  font-family: Ubuntu;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;

  @media (max-width: 450px) {
    flex-direction: column;
  }
}

.newsCard__wrapper--categoryAndDate {
  display: flex;
  @media (max-width: 450px) {
    justify-content: center;
    align-items: center;
  }
}

.newsCard__wrapper--comments {
  display: flex;
  justify-content: center;
  align-items: center;
}

.newsCard__text {
  margin-top: 16px;
  color: #424551;

  font-family: Ubuntu;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}

.newsCard__category {
  position: relative;
  margin-right: 25px;

  transition: color 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}

.newsCard__category:hover,
.newsCard__category:focus {
  color: #ff5a30;
}

.newsCard__category::after {
  content: "";
  position: absolute;
  bottom: 2px;
  right: -13px;
  background: #787a80;
  width: 1px;
  height: 12px;
}

.newsCard__date {
  position: relative;
  margin-right: 25px;

  @media (max-width: 450px) {
    margin-right: 0;
  }
}

.newsCard__date::after {
  content: "";
  position: absolute;
  bottom: 2px;
  right: -13px;
  background: #787a80;
  width: 1px;
  height: 12px;
}
@media (max-width: 450px) {
  .newsCard__date::after {
    display: none;
  }
}
.newsCard__comments {
  margin-left: 8px;
}
